.scrollTop {
    display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: flex-end;
	align-items: flex-end;
	align-content: flex-end;
    height: 40px;
    width: 40px;
}

.scrollTop:hover {
    cursor: wait;
    background-color: #12746c;
    border-radius: 50%;
    transform: scale(2.0);
}
