   .line-7 {
    background: linear-gradient(90deg, rgba(233,233,233,0.30155812324929976) 0%, rgba(247,247,240,0.5) 99%, rgba(245,244,222,0.5) 10%);
    height: 900px;
    width: 800px;
    position: absolute; 
    top : 0 ; 
    left: 1200px; 
   }

   .line-8 {
    animation: index-item1 6s forwards;
    animation-delay: calc(.1s * var(--i));
    animation-iteration-count: 1;
    height: 5px;
    width: 700px;
    position: absolute;
    top : 950px; 
    right: 300px ; 
    background-color: black;
   }

   .line-9 {
    animation: index-item2 6s forwards;
    animation-delay: calc(.1s * var(--i));
    animation-iteration-count: 1;
    height: 500px;
    width: 5px;
    position: absolute;
    top : 0px; 
    right: 350px ; 
    background-color: black;
    transform: rotate(450deg);
   }

   @keyframes index-item1 {
    from {
        transform: translateX(100%);
    }

    to {
        transform: translateX(0%);

    }
}

@keyframes index-item2 {
  from {
      transform: translateY(0%);
  }

  to {
      transform: translateY(100%);

  }
}

